.fa-times{color:#fff; font-size: 2rem;}

.nav-links-mobile{
display:block;
text-align: center;
margin: 2rem auto;
border-radius: 4px;
width : 80%;
text-decoration: none;
font-size: 1.5rem;
background-color: transparent;
color: #fff;
padding: 14px 20px;
border : 1px solid #fff;
transition : all 0.3s ease-out;
}

.nav-links-mobile :hover{
   background: #fff;
   color:#242424;
   transition : all .03m ease-out
}

.fa-bars {
    color:#fff;
}

.nav-links-mobile{display: none;}
